.btn-yellow {
    background: $yellow;
    box-shadow: 0px 4px 4px rgba(135, 83, 25, 0.3);
    padding: 17px 30px;
    color: #fff;
    border-radius: 4px;
    font-weight: 700;
    cursor: pointer;
    transition: all .3s;
    max-width: 265px;
    display: flex;
    justify-content: center;
    align-items: center;

    text-transform: uppercase;

    &:hover {
        background: $green;
        box-shadow: 0px 2px 2px rgba(135, 83, 25, 0.3);
    }

}

.fixed .btn-green {
    padding: 15px 18px;
    color: $green;
    box-shadow: none;
    border-radius: 4px;
    font-weight: 700;
    background: transparent;
    border: 1px solid $green;

    &:hover {
        background: $green;
        color: #fff;
    }

}



@media only screen and (max-width : 1400px) {
    .fixed .btn-green {
        padding: 12px 15px;
        color: $green;
        box-shadow: none;
        border-radius: 4px;
        font-weight: 700;
        background: transparent;
        border: 1px solid $green;
        font-size: 14px;

        &:hover {
            background: $green;
            color: #fff;
        }

    }
}

@media only screen and (max-width : 1200px) {

    .btn-green,
    .fixed .btn-green {
        display: none;

    }


}
