.content {
    background: #fff;
    margin-top: -60px;

}

.contact_wrp {
    margin: 20px 0;
}

.contact_title {
    color: #9D9D9D;
    margin-bottom: 20px;
    font-size: 14px;
}

.contact_phone {
    display: block;
    font-weight: 600;
    font-size: 22px;
    text-decoration: none;
    margin-bottom: 10px;
    color: $fontb;

    &:hover {
        color: inherit;
    }
}

.contact_info {
    line-height: 1.5;
}

.contact_mail {
    color: $fontl;

    &:hover {
        color: $green;
    }
}

.block--contacts {
    margin: 30px 0;
    display: flex;
    align-items: center;
}

.contact_txt {
    margin: 30pxpx;

}

.questions_block--text {
    margin-left: 10px;
    color: $fontl;
}

.office_img {


    img {
        height: 456px;
        object-fit: cover;

    }
}

.q_link {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: linear-gradient(90deg, rgba(255, 195, 40, 1) 18%, rgba(255, 141, 0, 1) 100%);
    display: flex;
    justify-content: center;
    align-items: center;


}

.ic_quest {
    width: 17px;
    height: 17px;
    fill: #fff;
}



@media only screen and (min-width: 992px) and (max-width: 1200px) {
    .contact_phone {

        font-size: 18px;

    }
}

@media only screen and (max-width : 768px) {
    .office_img {
        margin: 15px 0;


        img {
            height: auto;
            object-fit: cover;


        }
    }

    .contact_block3 {
        order: 1;

    }

    .contact_block2 {
        order: 2;

    }

    .contact_block1 {
        order: 3;

    }

}
