.heading-page {
    background: url('../img/heading.png');
    padding: 30px 0px 70px 0px;
    margin: 40px 0;

    h1 {

        font-size: 28px;
        color: #000;
        line-height: 1.3;
        margin-bottom: 20px;
        margin-top: 0;
        font-weight: 600;
    }

}

.breadcrumbs {
    li {
        display: inline-block;
        font-size: 15px;
        color: gray;

        a {
            color: #000;
            text-decoration: none;

            &:hover {
                color: gray
            }
        }
    }

    li + li {
        &::before {
            color: gray;
            content: "/";
            padding: 0 5px;
        }

    }
}

@media only screen and (max-width : 992px) {


    .heading {
        padding: 90px 0px 90px 0;
    }

}
