.services-wrp {
    /* background: #F0F3F8;*/
    background: url(../img/bg3.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}

@media only screen and (min-width : 2500px) {
    .services-wrp {
        background: url(../img/bg3@2x.jpg);
        background-size: cover;
        background-repeat: no-repeat;
    }
}

.title_block {
    text-align: end;
    font-weight: 600;
    font-size: 36px;
    line-height: 52px;
    color: $fontb;
    display: flex;
    justify-content: flex-end;
    padding-left: 300px;
    margin-bottom: 40px;
}

.green-block {
    background: #DDECEE;
    box-shadow: 0px 6px 16px rgba(198, 196, 217, 0.7);
    border-radius: 4px;
    padding: 20px;
    margin-bottom: 40px;


    &__title {
        font-size: 24px;
        line-height: 32px;
        color: #000;
        margin-bottom: 20px;

    }

    &__subtitle {
        font-size: 18px;
        line-height: 28px;
        color: #1E9E87;

    }
}

.dashed-block {
    border: 2px dashed #84D2C4;
    padding: 20px;
    border-radius: 4px;
    margin-bottom: 40px;

    &--title {
        font-size: 19px;
        line-height: 24px;
        color: #0B1F33;
        font-weight: 700;
        margin-bottom: 20px;

    }

    &--price {
        display: flex;
        align-items: center;
        background: #84D2C4;
        box-shadow: 0px 4px 4px rgba(114, 180, 169, 0.3);
        border-radius: 19px;
        color: #fff;
        height: 80px;
        padding: 0 15px;
        margin-bottom: 20px;

    }

    &--price--txt {
        font-weight: 700;
        font-size: 14px;
        line-height: 22px;
        text-transform: uppercase;
        height: 100%;
        border-right: 2px solid #fff;
        display: flex;
        align-items: center;
    }

    &--price:hover {
        background: $green;
        color: #fff;
        text-decoration: none;
    }

    .ic_btn {
        width: 30px;
        height: 30px;
        fill: #fff;
        margin-left: 15px;
    }

    .btn-radius {
        border-radius: 19px;
        height: 80px;

    }
}

.service {
    position: relative;
    background: linear-gradient(180deg, #FFFFFF 54.69%, rgba(255, 255, 255, 0) 86.98%);
    filter: drop-shadow(0px 6px 16px #E6EBF5);
    border-radius: 4px;
    margin-bottom: 20px;
    width: 100%;


    &__img {
        width: 100%;
        height: 200px;
        margin-bottom: 20px;

        img {
            display: block;
            width: 100%;
            object-fit: cover;
        }

    }

    &__title {
        font-weight: 400;
        font-size: 18px;
        line-height: 24px;
        color: #0B1F33;
        padding: 0 20px 20px 20px;
        background: #fff;
    }

    &__title--last {
        background: linear-gradient(180deg, #FFFFFF 54.69%, rgba(255, 255, 255, 0) 86.98%);

    }



    &__list {
        padding: 0 20px 0 40px;
        background: linear-gradient(180deg, #FFFFFF 54.69%, rgba(255, 255, 255, 0) 86.98%);
    }

    &__list ul {
        list-style-type: disc;
        font-weight: 700;
        font-size: 15px;
        line-height: 26px;
        color: #66727F;

    }
}

@media only screen and (max-width : 1200px) {
    .services-wrp {
        background: #F0F3F8;
    }
}

@media only screen and (max-width : 894px) {
    .title_block {
        text-align: end;
        font-weight: 600;
        font-size: 22px;
        line-height: 36px;
        color: $fontb;
        display: flex;
        justify-content: flex-start;
        padding-left: 0px;
        margin-bottom: 40px;
        padding-top: 40px
    }



}

@media only screen and (max-width : 576px) {
    .service {
        background: #fff;

        &__img {
            height: auto;

            img {
                height: 150px;
            }
        }

        &__title--last {
            background: #fff;

        }

        &__list {
            margin-bottom: 30px;
        }
    }
}
