header {
    background: #fff;
}

.header-wrp {
    display: flex;
}

.logo {
    margin-right: 15px;

    img {
        max-width: 158px;

    }
}

.header-info {
    display: flex;
    flex-direction: column;
    width: 100%;

}

.header-info__top {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    border-bottom: 2px dashed #ececec;


}

.descriptor {
    max-width: 450px;
    font-size: 15px;
    font-weight: 800;
    line-height: 1.4;
    color: $fontb;
    text-transform: uppercase;

}

.descriptor--right {
    font-size: 16px;
    line-height: 19px;
    text-align: right;
    color: rgba(11, 31, 51, 0.5);
    text-transform: none;
    font-weight: 600;
}

.header-info__bottom {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
}

.h-adress {
    display: flex;
    font-weight: 700;


}

.ic_header {
    width: 16px;
    height: 16px;
    fill: $yellow;
    margin-right: 7px;
}

.ic-marker {
    margin-top: 2px;
}

.h-email a {
    color: #66727F;
    line-height: 1;
    font-weight: 700;

    &:hover {
        color: $green;
    }
}

.ws-phone {
    display: flex;
}

.ic_ws {
    width: 16px;
    height: 16px;
    fill: $green;
    margin-right: 10px;
    margin-top: 3px;
}

.big-phone {
    font-size: 22px;
    font-weight: 700;
    color: $fontb;
    margin-bottom: 7px;
    display: block;
}

.container-menu {
    background: linear-gradient(270.01deg, rgba(0, 0, 0, 0) 12.69%, rgba(51, 51, 51, 0.07) 49.52%, rgba(0, 0, 0, 0) 112.65%);

}

.fixed {
    padding: 5px 0;

    .ws-phone p {
        font-size: 14px;
    }

}

.fixed_visible {
    display: none;
}

.fixed .mmenu-wrp,
.fixed .fixed_visible {
    display: flex;
    align-items: center;
}

.fixed .mmenu-wrp {
    justify-content: space-between;
}

.fixed .big-phone {
    font-size: 18px;
    font-weight: 700;
    color: $fontb;
    margin-bottom: 7px;
    display: block;
}

.fixed_visible .h-phones {
    margin-right: 15px;
}

.tehsnab {
    color: $fontb;
    font-weight: 700;
    margin-left: 40px;
    font-size: 20px;
    margin-top: -3px;
}

.main-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;

}


@media only screen and (max-width : 1400px) {
    .h-adress p {
        font-size: 14px;
    }

    .h-email {
        font-size: 14px;
    }

    .big-phone {
        font-size: 18px;
    }

    .ws-phone p {
        font-size: 12px;
    }

    .descriptor {
        font-size: 14px;

    }

    .sf-menu > li > a {
        font-size: 14px;
    }

}

.fixed {
    .mmenu-wrp {
        width: 100%;
    }

    .main-wrapper {
        padding-top: 3px;
    }
}

@media only screen and (max-width : 1200px) {
    .fixed .fixed_visible {
        display: none;
    }
}

@media only screen and (max-width : 990px) {
    .header-wrp {
        display: none;
    }

    .fixed {

        .main-wrapper {
            padding-top: 0px;
        }
    }


}
