.cd-main-content {

    padding: 30px 0px 20px 0;

}

.cd-filters {
    display: flex;
    flex-wrap: wrap;
}

.filter {
    margin-bottom: 40px;

    &:last-child {
        margin-right: 0;
    }

    a {
        padding: 16px 24px 15px;
        color: #000;
        border: 1px solid #ececec;
        margin-right: 15px;
        border-radius: 3px;
        background: #efefef;
        color: #000;
        transition: all .3s;
        margin-bottom: 20px;
        text-decoration: none;


        &:hover {
            color: $green;
        }

    }
}

.mix {
    display: none;
}


.case_block {
    position: relative;
    margin: 20px 0;
    border-radius: 8px;
    overflow: hidden;


    img {
        object-fit: cover;
        width: 100%;
        height: 240px;
        display: block;
    }

}

.case_block__info {
    position: absolute;
    bottom: 0;
    background: linear-gradient(rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.9) 100%);
    width: 100%;
}

.case_title {
    color: #fff;
    font-weight: 300;
    font-size: 13px;
    display: inline-block;
    line-height: 1.3;
    padding: 25px 10px 10px 10px;
    text-transform: uppercase;
}

.case_info {
    color: #fff;
    font-weight: 700;
    font-size: 24px;
    margin-bottom: 20px;
    line-height: 1.3;
    padding: 0 10px;



}

.filter_items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.mix {
    flex-basis: 31%;

}

.selected {
    border-bottom: 2px solid $green !important;
}

@media only screen and (max-width : 768px) {
    .mix {
        flex-basis: 50%;

    }

}

@media only screen and (max-width : 540px) {
    .mix {
        flex-basis: 100%;

    }

}
