.mobile_buttons {
    display: none;
}

@media only screen and (max-width : 990px) {

    .mobile_buttons {
        display: flex;
        justify-content: flex-end;
        align-items: center;

        a {
            background: $green;
            width: 47px;
            height: 47px !important;
            margin-right: 2px;
            height: auto;
            display: flex;
            justify-content: center;
            align-items: center;

        }

        .ic_mob {
            width: 15px;
            height: 15px;
            fill: #fff;
        }
    }
}
