.container-menu {
    top: -220px;
}

.fixed {
    transition: top 1s;
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 60;
    left: 0;
    right: 0;
    margin: auto;
    max-width: 1320px;
    padding: 0 0;
    z-index: 100;

    &::before {
        content: "";
        width: 3000px;
        height: 56px;
        background: #fff;
        position: absolute;
        top: 0;
        left: -1000px;
        display: block;
        z-index: -1;
        box-shadow: 0 2px 10px #ececec;
    }


    .toggle-mnu span,
    .toggle-mnu span::after,
    .toggle-mnu span::before {
        background-color: $fontb;
    }


}

.mfixed {

    margin-top: 180px;
}



@media only screen and (max-width : 990px) {
    .fixed {

        &::before {
            content: "";
            width: 2000px;
            height: 53px;

        }

    }
}
