.vacan {
    &__top {
        padding: 15px;
        background: #FAFCFF;

    }

    &__wrp {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: 20px;

    }

    &__btn {
        padding: 17px 14px;
        color: #fff;
        background: $green;
        border-radius: 4px;
        margin-bottom: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
    }

    &__tit {
        display: flex;
        flex-direction: column;
        margin-bottom: 10px;
    }

    &__uppertext {
        color: $fontl;
        font-size: 14px;
        margin-bottom: 10px;
    }

    &__info {
        font-weight: 700;
        font-size: 18px;
        color: $fontb;
    }

    &__txt p {
        font-size: 18px;
        line-height: 1.4;
    }

}

.vacan {
    .dropdownlink {
        background: #F0F3F8;
        align-items: center;
    }

    &__infowrap {
        display: flex;
        flex-direction: column;
        flex-basis: 80%;
    }

    &__title {
        font-size: 20px;
        margin-bottom: 10px;
        color: $fontb;
        font-weight: 700;
    }

    &__subtitle {
        color: $fontl;
        font-size: 16px;
        font-weight: 400;
    }

    &__salary {
        font-size: 20px;
        color: $fontb;
        font-weight: 700;
        flex-basis: 10%;
    }

    .ic_faq {
        flex-basis: 10%;
    }

    h4 {
        font-size: 18px;
        font-weight: 700;
        margin: 10px 0;
    }

    .vacan__ul {
        list-style-type: disc;
        padding-left: 20px;

        li {
            padding: 7px 0;
        }
    }

    .question_info {
        background: #FAFCFF;
    }
}

@media only screen and (max-width : 576px) {
    .vacan {
        .dropdownlink {
            flex-wrap: wrap;
        }

        &__infowrap {

            flex-basis: 100%;
            margin-bottom: 10px;
        }

        &__salary {
            font-size: 20px;
            color: $fontb;
            font-weight: 700;
            flex-basis: 50%;
        }

        .ic_faq {
            flex-basis: 10%;
        }

        &__btn {
            width: 100%;
        }
    }

}
