.block_title {
    text-align: end;
    text-transform: uppercase;
    color: $fontb;
    font-weight: 700;
    padding: 20px 0;
    font-size: 19px;
}



.partners {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .partner {
        flex-basis: 18%;
        height: 90px;
        display: flex;
        justify-content: center;
        align-items: center;


        span {
            display: flex;
            align-self: center;

            img {
                max-width: 100%;
                height: auto;
            }
        }

    }
}

@media only screen and (max-width : 576px) {
    .partners {
        .partner {
            flex-basis: 48%;
        }
    }
}
