.star-rating {
    font-size: 20px;
}

.star-rating__wrap {
    display: inline-block;
    font-size: 20px;
}

.star-rating__wrap:after {
    content: "";
    display: table;
    clear: both;
}

.star-rating__ico {
    float: right;
    padding-left: 2px;
    cursor: pointer;
    color: #FFB300;
}

.star-rating__ico:last-child {
    padding-left: 0;
}

.star-rating__input {
    display: none;
}

.star-rating__ico:hover:before,
.star-rating__ico:hover ~ .star-rating__ico:before,
.star-rating__input:checked ~ .star-rating__ico:before {
    content: "\f005";
}

.star-rating-title {
    margin: 20px 0;
    font-size: 14px;
    color: $fontl;
}
