.service-item {
    padding: 20px;
    border: 1px solid #ececec;
    margin-bottom: 20px;

    &__title {
        padding: 0;
        margin-bottom: 10px;
        font-weight: 600;
    }

    &__list {
        padding-left: 20px;
        margin-bottom: 20px;
    }

    &__btn {
        min-width: 174px;
        background: #F0F3F8;
        border: 1px solid #E2E2E2;
        font-size: 16px;
        color: $fontb;
        padding: 10px;
        display: inline-flex;
        justify-content: center;
        align-items: center;

        span {
            margin-right: 10px;
        }

        &:hover {
            color: $fontl
        }
    }

    .ic_more {
        width: 5px;
        height: 10px;
        margin-top: 4px;
    }
}

@media only screen and (max-width : 576px) {
    .service-item {
        padding: 0px;
        border: none;

        &__img {
            margin-bottom: 15px;
        }

    }
}
