.display1 {
    background: url('../img/bg.jpg');
    position: relative;
    z-index: 10;
    padding-bottom: 100px;
    background-size: cover;
    background-repeat: no-repeat;
}


@media only screen and (min-width : 2500px) {
    .display1 {
        background: url(../img/bg5@2x.jpg);
        background-size: cover;
        background-repeat: no-repeat;
        position: relative;
        z-index: 10;
        background-position-y: -250px;
        background-color: #f0f3f8;
    }
}




.tizers {
    background: #FFFFFF;
    box-shadow: 0px 6px 16px #E6EBF5;
    border-radius: 4px;
    padding: 15px;
    margin-top: 42px;
}

.tizer {
    margin-bottom: 20px;

    &__img {
        margin-bottom: 10px;
    }

    &__txt {
        line-height: 1.4;
        color: $fontb;
        font-weight: 700;
    }

    span {
        text-transform: uppercase;
        display: block;
        color: $green;
        font-weight: 800;
    }
}

.maintxt {
    padding: 84px 296px 164px 46px;

    &__top {
        width: 155px;
        height: 44px;
        padding: 12px 15px;
        background: #66727F;
        color: #fff;
        font-size: 16px;
        text-transform: uppercase;
        display: inline-block;
        text-align: center;
        border-radius: 4px;
        margin-bottom: 46px;
        box-shadow: 0px 3px 5px rgba(4, 4, 4, 0.3);
    }

    &__heading h1 {
        font-weight: 600;
        font-size: 36px;
        line-height: 56px;
        color: $fontb;
    }

    &__desc {
        font-size: 18px;
        line-height: 28px;
        color: $fontl;
        margin-bottom: 46px;
    }
}

@media only screen and (max-width : 1200px) {
    .maintxt__btn {
        display: inline-block;
    }

    .order1 {
        order: 2;
    }

    .order2 {
        order: 1;
    }

    .maintxt {
        padding: 40px 40px 60px 0px;

        &__top {
            margin-bottom: 26px;
        }

        &__heading h1 {
            font-weight: 600;
            font-size: 36px;
            line-height: 1.4;
            margin-bottom: 20px;
            color: $fontb;
        }

        &__desc {
            line-height: 1.4;
            margin-bottom: 20px;
        }
    }

    .tizers {
        display: flex;
    }

    .tizers--page {
        flex-direction: column;
    }
}

@media only screen and (max-width : 576px) {
    .display1 {
        background: #fff;
        padding-bottom: 0;
    }

    .maintxt {
        padding: 40px 15px 20px 15px;

        &__top {
            margin-bottom: 26px;
        }

        &__heading h1 {
            font-weight: 600;
            font-size: 20px;
            line-height: 1.4;
            margin-bottom: 20px;
            color: $fontb;
        }

        &__desc {
            line-height: 1.4;
            margin-bottom: 20px;
        }
    }

    .tizers {
        flex-wrap: wrap;
        align-items: center;
    }

    .tizer {
        display: flex;

        &__img {
            margin-bottom: 10px;
            flex-basis: 20%;
            margin-right: 10px;
        }

        &__txt {
            line-height: 1.4;
            color: $fontb;
            font-weight: 700;
            flex-basis: 80%;
        }

        span {
            text-transform: uppercase;
            display: block;
            color: $green;
            font-weight: 800;
        }
    }

}
