.testim-top {
    padding: 15px;
    border: 1px solid #ececec;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 20px;
    justify-content: space-between;

    &__kav {
        margin-right: 7px 15px 7px 0;
        flex-basis: 10%;

        img {
            width: 44px;
            height: 41px;

        }
    }

    &__txt {
        font-size: 18px;
        line-height: 27px;
        color: #66727F;
        flex-basis: 60%;
        padding: 7px 0;
    }

    &__btn {
        margin: 7px 0;
        flex-basis: 25%;
        padding: 15px 18px;
        color: #fff;
        box-shadow: none;
        border-radius: 4px;
        font-weight: 700;
        background: #84D2C4;
        box-shadow: 0px 4px 4px rgba(114, 180, 169, 0.25);
        text-align: center;
        justify-content: center;
        font-size: 18px;
        line-height: 1;
        width: 100%;
        cursor: pointer;

        &:hover {
            background: $yellow;
            color: #fff;
        }
    }

}

.testimonials {
    display: flex;
    flex-direction: column;
}

.testimonial {
    position: relative;
    background: #FFFFFF;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
    padding: 20px;


    &__rating-5 {
        &:after {
            content: "";
            position: absolute;
            display: block;
            right: 20px;
            top: 20px;
            background: url('../img/rating5.png');
            width: 130px;
            height: 18px;

        }
    }

    &__rating-4 {
        &:after {
            content: "";
            position: absolute;
            display: block;
            right: 20px;
            top: 20px;
            background: url('../img/rating4.png');
            width: 130px;
            height: 18px;

        }
    }

    &__rating-3 {
        &:after {
            content: "";
            position: absolute;
            display: block;
            right: 20px;
            top: 20px;
            background: url('../img/rating3.png');
            width: 130px;
            height: 18px;

        }
    }

    &__rating-2 {
        &:after {
            content: "";
            position: absolute;
            display: block;
            right: 20px;
            top: 20px;
            background: url('../img/rating2.png');
            width: 130px;
            height: 18px;

        }
    }

    &__rating-1 {
        &:after {
            content: "";
            position: absolute;
            display: block;
            right: 20px;
            top: 20px;
            background: url('../img/rating1.png');
            width: 130px;
            height: 18px;

        }
    }

    &__main {
        display: flex;
    }

    &__logo {
        flex-basis: 20%;

        img {
            max-width: 86px;
            height: auto;

        }
    }

    &__info {
        flex-basis: 75%;
    }

    &__post {
        font-size: 14px;
        color: #66727F;
        margin-bottom: 20px;
        line-height: 1.4;
    }

    &__name {
        font-weight: 700;
        font-size: 18px;
        color: #000000;
        margin-bottom: 20px;
        line-height: 1.4;
    }

    &__text {
        font-weight: 400;
        font-size: 16px;
        color: #66727F;
        margin-bottom: 20px;
        line-height: 1.4;

    }

    &__gallery {
        display: flex;
        margin-bottom: 20px;

        a {
            margin-right: 10px;
            cursor: pointer;
        }

        a img {
            width: 50px;
            height: 70px;
            object-fit: cover;
        }
    }

    &__answer {
        background: #F2F2F2;
        display: flex;
        padding: 20px;
    }

    &__tehsnab {
        flex-basis: 20%;

        img {
            max-width: 86px;
            height: auto;
        }
    }

    &__answer-title {
        font-size: 14px;
        color: #66727F;
        margin-bottom: 20px;
        line-height: 1.4;

    }

    &__answer-brand {
        font-weight: 700;
        font-size: 18px;
        color: #000000;
        margin-bottom: 20px;
        line-height: 1.4;
    }

    &__answer-text {
        font-weight: 400;
        font-size: 16px;
        color: #66727F;
        margin-bottom: 20px;
        line-height: 1.4;


    }
}

@media only screen and (max-width : 1200px) {
    .testimonial {
        padding-top: 60px;
    }

}


@media only screen and (max-width : 768px) {
    .testim-top {
        padding: 0;
        border: none;

        &__kav {
            display: none;
        }

        &__txt {
            flex-basis: 100%;
        }

        &__btn {
            flex-basis: 100%
        }
    }
}

@media only screen and (max-width : 576px) {
    .testimonial {
        &__main {
            flex-wrap: wrap;
        }

        &__logo {
            flex-basis: 100%;
            margin-bottom: 20px;
        }

        &__info {
            flex-basis: 100%;
        }

        &__answer {
            flex-wrap: wrap;
        }

        &__answer-brand {
            margin-bottom: 20px;
        }

        &__tehsnab {
            flex-basis: 100%;
        }

        &__answer-info {
            flex-basis: 100%;
        }
    }


}
