.content-img {
    margin-bottom: 20px;
}

.main-text {
    padding: 20px 15px 20px 40px;
    border-left: 3px solid $green;
    background: #FAFAFA;
    margin-bottom: 20px;
    line-height: 1.5;
}

.content-h2 {
    font-weight: 700;
    font-size: 24px;
    line-height: 1.4;
    color: #0B1F33;
    margin: 10px 0;
}

.content-h3 {
    font-weight: 700;
    font-size: 24px;
    line-height: 1.4;
    color: #0B1F33;
    margin: 10px 0;

}

.content-list {

    line-height: 1.5;
    color: #66727F;
    margin: 10px 0;

    ul {
        list-style-type: disc;
        padding-left: 40px;

        li {
            padding: 5px 0;

        }
    }

    ol {
        list-style-type: decimal;
        padding-left: 40px;

        li {
            padding: 5px 0;
        }

    }

}
