}

.menu-new-item {
    margin-bottom: 30px;
}

.menu-new-item h3 {
    margin: 15px 0;
}

.menu-new-item a {
    padding: 0;
    font-size: 18px;
    text-decoration: underline;
    color: $blue;
}

.menu-new-item p {
    font-size: 14px;
    color: #666;
}

.sf-menu > li > a + * .menu-new-item a {
    border-bottom: none;
}

.sf-menu > li > a + * .menu-new-item a:hover {
    color: #333;
}

.mega-sub img {
    display: inline-block;
    margin-right: 10px;
    max-width: 30px;
    height: auto;
}

.small-sub {
    background: #F3F3F3;
    text-align: center;

    li {
        text-align: center;

        &:hover {
            background: #F3F3F3;

        }
    }

    li a {
        transition: none !important;
        color: $fontb;
        text-align: center;

        &:hover {
            box-shadow: 0px 4px 4px #84D2C4;
            background: #F3F3F3;
            color: $fontb;
        }
    }
}

.small-sub img {
    display: inline-block;
    margin-right: 10px;
    max-width: 30px;
    height: auto;
}


.mega-sub a {
    display: flex;
    align-items: center;
}

.small-sub a {
    display: flex;
    align-items: center;
    justify-content: center;
}


.mobile_item {
    display: none;
}

html.mm-pagedim.mm-opening #mm-blocker,
html.mm-pagedim-white.mm-opening #mm-blocker,
html.mm-pagedim-black.mm-opening #mm-blocker {
    opacity: 0.3;
    transition: opacity 0.4s ease 0.4s;



    &::after {
        content: "";
        background: url(../img/ic-close.svg) no-repeat 0 0;
        color: #fff;
        display: block;
        width: 20px;
        height: 20px;
        position: absolute;
        z-index: 99999;
        top: 10px;
        left: -30px;


    }
}



@media only screen and (max-width: 990px) {
    .on {
        position: fixed;
        left: 0;
        top: 0;
        width: 20px;
        background: #00000073;
        margin-top: 30px;
        height: 20px;
        color: #fff;
        padding-left: 3px;
        display: none;

        span:after,
        span:before {
            background-color: #fff;

        }

    }



    .header_wrp {
        display: flex;
        justify-content: flex-end;
        position: fixed;
        right: 15px;

    }

    .top-menu_block,
    .search_block {
        display: none;
    }

    .dropdwn:after {
        display: none;
    }

    .mobile_item {
        display: flex !important;
    }

    .top-mnu ul {
        display: none;
    }

    .header-wrap {
        padding-bottom: 10px;
    }
}


@media only screen and (min-width: 991px) {
    .toggle-mnu {
        display: none;
    }

    .toggle-mnu,
    #mobile-menu,
    html.mm-blocking #mm-blocker {
        display: none;
    }

    html.mm-opening .mm-slideout {
        -webkit-transform: translate(0px, 0px);
        -ms-transform: translate(0px, 0px);
        transform: translate(0px, 0px);
    }
}

.mega-sub {
    top: 0px !important;
}


.toggle-mobile {
    margin-right: 20px;
    margin-top: 6px;
    margin-bottom: 0;
    display: none;
}

@media only screen and (max-width: 991px) {
    .fixed .toggle-mobile {
        display: inline-block;
    }
}

.sfHover .sf-with-ul {
    color: #fff !important;
}
