.collect__arrows {
    margin-bottom: 20px;
    position: relative;
    z-index: 600;
}

.collect__arrows .pr-prev1 {
    width: 103px;
    height: 46px;
    background: #F2EDEA;
    font-size: 20px;
    color: #66727F;
    border: none;
    border-radius: 4px;
    margin-right: 10px;
}

.collect__arrows .pr-next1 {
    width: 103px;
    height: 46px;
    background: #F2EDEA;
    font-size: 20px;
    color: #66727F;
    border: none;
    border-radius: 4px;
}

.gallery-slider {
    margin-top: 30px;

}

.g-slider {
    margin-right: -5px;
    margin-left: -5px;

}

.g-slide {
    margin: 5px;
    border: 8px solid #f2edea;
    display: block;

}

.g-slide img {
    height: 300px;
    display: block;
    object-fit: cover;
    cursor: pointer;

}
