.news-title {
    max-width: 800px;
    margin-bottom: 40px;
    margin-top: 60px;

    &__yellow {
        font-weight: 700;
        font-size: 19px;
        line-height: 25px;
        text-transform: uppercase;
        color: #FCB720;
        margin-bottom: 30px;
    }

    &__middle {
        font-weight: 700;
        font-size: 36px;
        color: #0B1F33;
        margin-bottom: 30px;
    }

    &__gray {
        background: #66727F;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 4px;
        font-weight: 400;
        font-size: 16px;
        padding: 12px 5px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #FFFFFF;
        width: 182px;

    }
}

.news-item {
    background: #FFFFFF;
    box-shadow: 0px 6px 16px #E6EBF5;
    border-radius: 4px;
    overflow: hidden;
    margin-bottom: 15px;
    position: relative;

    &__wrp {
        padding: 20px 20px 80px 20px;
    }

    &__title {
        font-weight: 400;
        font-size: 18px;
        line-height: 24px;
        display: flex;
        align-items: center;
        color: #0B1F33;
        margin-bottom: 20px;

        &:hover {
            color: $green;
        }
    }

    &__date {
        font-weight: 700;
        font-size: 12px;
        line-height: 26px;
        color: #66727F;
        margin-bottom: 20px;
    }

    &__text {
        font-weight: 700;
        font-size: 15px;
        line-height: 26px;
        color: #66727F;
        margin-bottom: 20px;
    }

    &__insta {
        display: flex;
        margin-bottom: auto;

        span {
            font-weight: 700;
            font-size: 15px;
            line-height: 26px;
            color: #0B1F33;
            margin-left: 15px;
        }

        &:hover span {
            color: $green;
        }
    }

    &__more {
        display: flex;
        padding: 18px 5px;
        width: 100%;
        background: #84D2C4;
        align-items: center;
        justify-content: center;
        position: absolute;
        left: 0;
        bottom: 0;

        .ic_more {
            fill: #fff;
            width: 30px;
            height: 20px
        }

        span {
            color: #fff;
        }

        &:hover {
            background: $yellow;
        }
    }
}

.news-title {
    max-width: 100%;
    margin-bottom: 40px;


    &__middle {
        line-height: 1.4;
        font-size: 20px;

    }


}
