.slick-dots {
    display: flex !important;
    flex-direction: row;
    justify-content: center;
    align-items: center;

}

.slick-dots > li {
    width: 13px;
    height: 13px;
    margin: 0 10px;
}

.slick-dots > li > button {

    background: #fff;
    width: 19px;
    height: 19px;
    border-radius: 50%;
    border: 2px solid $yellow;
    font-size: 0;


    &:hover {
        background: #fff;
    }

}

.slick-dots > li.slick-active > button {
    background: $green;
    border: 2px solid $green;

    &:hover {
        background: #fff;
    }

}

@media only screen and (max-width : 576px) {
    .slick-dots > li {
        width: 13px;
        height: 13px;
        margin-top: 40px;
    }

}
