// 

.page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}


hr {
    background-color: #fff;
}

.footer {
    position: relative;
    margin-top: auto;
    padding: 200px 0 40px 0;
    background-color: red;
    background: url('../img/footer.png');
    background-repeat: no-repeat;
    background-size: cover;


    &__logos {
        img {
            margin-bottom: 15px;
        }

    }

    &__social {
        margin-top: 30px;
        display: flex;
        opacity: .9;
        margin-bottom: 20px;

        a {
            color: #fff;
            font-size: 16px;
        }

        .ic_social {
            width: 25px;
            height: 25px;
            fill: #fff;
            margin-right: 10px;
        }

        &:hover {
            opacity: 1;
        }
    }

    &__socialtext {
        color: #fff;
        opacity: .5;
    }


    &__descriptor {
        font-weight: 400;
        font-size: 16px;
        line-height: 1.4;
        color: #000;

    }


    &__title {
        color: #000;
        font-size: 18px;
        margin-bottom: 30px;
        font-weight: 600;
        text-transform: uppercase;
    }

    &__phones {
        margin-bottom: 30px;
    }

    &__phone {
        display: block;
        color: #000;
        font-size: 18px;
        margin-bottom: 10px;
        font-weight: 600;

    }

    &__ws {
        margin-bottom: 20px;

        a {
            font-size: 16px;
            color: #000;
            opacity: .8;

            &:hover {
                opacity: 1;
            }
        }
    }

    &__contact {
        display: flex;
        margin-bottom: 20px;


        a {
            font-size: 16px;
            color: #000;
            opacity: .8;
            margin-bottom: 15px;
            display: block;
            line-height: 1.4;

            &:hover {
                opacity: 1;
            }

        }
    }

    &__ic {
        width: 17px;
        height: 17px;
        fill: $yellow;
        margin-right: 10px;
        margin-top: 5px;
    }

    &__menu {
        margin-bottom: 30px;

        a {
            font-size: 16px;
            color: #000;
            opacity: .8;
            margin-bottom: 15px;
            display: block;
            line-height: 1.4;

            &:hover {
                opacity: 1;
            }

        }
    }

    &__info {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-start;
    }

    &__privacy {
        color: rgba(0, 0, 0, 0.25);
        font-size: 12px;
        margin-top: 30px;
    }



}

.payment {
    display: flex;

    &__item {
        margin-right: 11px;
    }
}

.dashed {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    align-items: center;
    border-top: 2px dashed #838383;
    border-bottom: 2px dashed #838383;
    padding: 4px 0;
    margin-bottom: 10px;
    font-size: 10px;

    &__social {
        display: flex;
        align-items: center;

        a {
            margin: 0 3px;

        }
    }

    &__social_title {
        text-transform: uppercase;
        margin-right: 10px;
    }

    &__privacy {
        text-transform: uppercase;
        color: #000;

        &:hover {
            color: $yellow;
        }
    }
}

.scrollup {
    width: 66px;
    height: 66px;
    opacity: 0.3;
    position: fixed;
    bottom: 115px;
    right: 100px;
    display: none;
    text-indent: -9999px;
    background: url("../img/up.svg") no-repeat;
    background-size: cover;
    background-repeat: no-repeat;
    transition: all .3s;

    &:hover {
        opacity: 1;
    }
}



@media only screen and (max-width : 1200px) {
    .footer {

        padding: 40px 0 40px 0;

        background: linear-gradient(226.61deg, rgba(110, 183, 170, 0.3) 0.99%, rgba(34, 119, 104, 0) 59.14%);
    }
}

@media only screen and (max-width : 768px) {
    .dashed {
        margin: 10px 0;
    }

}

@media only screen and (max-width : 560px) {
    .footer {
        &__privacy {
            flex-basis: 100%;
            margin-bottom: 20px;
            margin-top: 0;
        }


        &__title {
            margin-bottom: 20px;
            font-size: 16px;
        }

        &__menu {

            a {
                font-size: 14px;
                line-height: 1.3;
            }
        }

        &__phone {
            font-size: 16px;
        }

        &__contact {
            a {
                font-size: 14px;
                margin-bottom: 0px;
            }
        }

        &__ic {
            margin-top: 0;
        }

        &__descriptor {
            margin-bottom: 20px;
        }
    }

    .dashed {
        &__privacy {
            margin: 15px 0;
        }

        &__social {
            margin: 10px 0;

            img {
                width: 20px;
                height: 20px;
            }
        }
    }

}

@media only screen and (max-width : 1200px) {
    .scrollup {
        right: 10px;
    }
}

@media only screen and (max-width : 768px) {
    .scrollup {
        opacity: 0;
    }
}
