.buttons_block {
    margin: 20px 0;

    .calc_price_btn {
        padding: 20px 20px;
        display: flex;
        font-size: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-transform: uppercase;
        transition: all .3s;
        cursor: pointer;
        font-weight: 600;
        background: #F0F3F8;
        border: 1px solid #E2E2E2;
        margin-bottom: 15px;


        &:hover {
            box-shadow: 2px 2px 10px #ececec;
        }

        .ic_sidebar-small {
            width: 17px;
            height: 17px;
            fill: $yellow;
            margin-right: 10px;
        }

    }
}

.sidebar-nav {
    li {
        border-bottom: 1px solid rgba(236, 236, 236, 1);
    }

    .active {
        a {
            font-weight: 600;
            color: $fontb;
        }
    }

    a {
        padding: 15px 5px 15px 15px;
        display: block;
        font-weight: 400;
        color: $fontl;
        line-height: 1.3;


        &:hover {
            color: $fontb;

        }
    }
}

@media only screen and (max-width : 1200px) {
    .buttons_block {

        .calc_price_btn {
            padding: 20px 5px;



        }
    }

}
